          heroImage="/vue-portfolio/media-tech/Schermafbeelding_2024-12-27_143717.png" 
          :disableUnfold="true"
        >
        For my thesis, I am using computational methods to discover new insights about a relatively unknown medical condition, called retrograde cricopharyngeal dysfunction (R-CPD). I have performed content analysis on 11.904 Reddit posts, from a subreddit called 'r/noburp'. This subreddit about R-CPD has over 38.000 members. Using Python, I extracted all posts. I cleaned them, tokenized them, lemmatized them, embedded them and subjected them to an LDA model. I found topics, labelled the posts in a 2-dimensional t-SNE plot and visualized them. I performed sentiment analysis on the posts and checked out other features of each posts, such as post length, number of comments and creation date.
        In my analysis, I found interesting insights. For example, I found that the longer posts are mostly negative ones, and I found that people tend to speak very positively and also very negatively in the posts about R-CPD treatments.
